		&-content {
			.tab-panel {
				display: none;
				&.active {
					display: block;
				}
			}
		}
		
	}


	@media only screen and (min-width: 768px) {
		&.side-tab-layout {
			display: flex;
			width: 100%;
			.tab-labels {
				display: flex;
				flex-direction: column;
				border-right: solid $light-gray 1px;
			}
			.tab-content {
				flex: 1;
				display: flex;
				flex-direction: column;
				.tab-panel.active {
					flex: 1;
					display: flex;
					flex-direction: column;
					.wp-block-cgb-block-page-section {
						flex: 1;
					}
					& > * {
						padding: 0 2rem;
					}
				}
			}
		}
	}
} //.wp-block-create-block-tabs


